.header {
  position: fixed;
  width: 100%;
  padding: 1rem 0;
}
.header article {
  max-width: 1280px;
  margin: 0 auto;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .logoArea {
  display: flex;
}
.header .logoArea img {
  max-width: 127px;
  width: 100%;
  display: flex;
}
.hamberger {
  width: 32px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.hamberger span {
  display: block;
  width: 100%;
  height: 1.5px;
  background-color: #fff;
}
